<template>
    <section class="dev-services">
        <div class="container-min">
            <!---------------------------------------block-------------------------------------->
            <div class="dev-services__block">
                <div class="dev-services__img violet-color">
                    <img src="dist/img/dev/vizitka.png" alt="">
                    <div class="dev-services__title"> </div>
                </div>
                <div class="dev-services__text">
                    <div class="dev-services__item">
                        <div class="dev-services__item--title">:</div>
                        <div class="dev-services__item--text">          </div>
                    </div>
                    <div class="dev-services__item">
                        <div class="dev-services__item--title">:</div>
                        <div class="dev-services__item--text">         
                                      
                               
                        </div>
                    </div>
                    <div class="dev-services__item">
                        <div class="dev-services__item--title">:</div>
                        <div class="dev-services__item--text"> 450$</div>
                    </div>
                    <div class="dev-services__item">
                        <div class="dev-services__item--title"> :</div>
                        <div class="dev-services__item--text"> 7  14 </div>
                    </div>
                    <div class="dev-services__item">
                        <div class="dev-services__item--title">:</div>
                        <div class="dev-services__item--text">1 - 3</div>
                    </div>
                    <div class="dev-services__item">
                        <div class="dev-services__item--title">  :</div>
                        <div class="dev-services__item--text">CMS Wordpress</div>
                    </div>
                    <div class="dev-services__item">
                        <div class="dev-services__item--title">  :</div>
                        <div class="dev-services__item--text">     ,  
                                 .
                        </div>
                    </div>
                </div>
                <div class="dev-services__order">
                    <div class="border-btn-box pointer-large">
                        <div class="border-btn-inner">
                            <button @click="showModal" data-text="" class="border-btn modal-show">
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Modal></Modal>
        <Footer></Footer>
    </section>
</template>

<script>
    import Order from '../components/Order'
    import Footer from '../components/Footer.vue'
    import Modal from '../components/Modal.vue'

    export default {
        name: 'vizitka',
        components: {
            Order,
            Footer,
            Modal,
        },
        methods: {
            showModal() {
                document.querySelector('.modal-wrap').classList.remove('hide');
            },
            closeModal() {
                document.querySelectorAll('.modal-wrap').forEach(function (element) {
                    element.classList.add('hide');
                });
            },
        },
    };
</script>


